.header {
    display: flex;
    width: 100%;
    height: 50px;
    padding: 5px 27vw;
    box-sizing: border-box;
    justify-content: space-between;
    align-items: center;
    background-color: #fc8507;
    -webkit-box-shadow: 0px 7px 8px 0px rgba(34, 60, 80, 0.2);
    -moz-box-shadow: 0px 7px 8px 0px rgba(34, 60, 80, 0.2);
    box-shadow: 0px 7px 8px 0px rgba(34, 60, 80, 0.2);
}

.header__logo {
    display: flex;
    align-items: center;
    column-gap: 8px;
    text-decoration: none;
}

.header__logo span {
    color: #ffffff;
    font-family: sans-serif;
    font-size: 20px;
}

.header__navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 48px;
    position: relative;
}

.header__navbar ul {
    display: flex;
    column-gap: 12px;
    list-style: none;
}

.header__navbar ul a {
    color: #ffffff;
    text-decoration: none;
}

.header__navbar ul a:hover {

}

.header__btnToCatalog {
    padding: 5px 15px;
    border-radius: 0.5em;
    color: #000000;
    background-color: #ffffff;
    -webkit-box-shadow: 0px 0px 5px 0px rgba(255,255,255,1);
    -moz-box-shadow: 0px 0px 5px 0px rgba(255,255,255,1);
    box-shadow: 0px 0px 5px 0px rgba(255,255,255,1);
    transition: 0.4s;
}

.header__btnToCatalog:hover {
    -webkit-box-shadow: 0px 0px 15px 0px rgba(255,255,255,1);
    -moz-box-shadow: 0px 0px 15px 0px rgba(255,255,255,1);
    box-shadow: 0px 0px 15px 0px rgba(255,255,255,1);
}

.header__btnToCart {
    display: flex;
    align-items: center;
    column-gap: 5px;
    transition: 0.4s;
}

.header__btnToCart:hover {
    color: #e5e5e5;
}

.header__countItemInCart {
    padding: 0 4px;
    border-radius: 2em;
    background-color: #ffffff;
    position: absolute;
    color: #000000;
    font-size: 12px!important;
    top: 10px;
    left: 48px;
}

.header__btnToCart img {
    width: 18px;
    height: 18px;
}

.header__btnToCart span {
    font-size: 18px;
}

@media screen and (max-width: 1340px) {
    .header {
        padding: 0 40px;
    }
    .header__navbar > * {
        margin-left: 0;
    }
}
